.wrapper_relation{
      width: 460px;
      height: 550px;
}

.relation_header{
      height: 208px;
      display: flex;
      align-items: center;
      gap: 1rem;
}

.relation_header__avatar,
.relation_header__avatar img{
      width: 168px;
      height: 168px;
      border-radius: 50%;
      display: flex;
}

.relation_header__avatar{
      margin: 2rem;
      background: var(--alpha);
      box-shadow: 0 0 0 1rem rgba(0,0,0,0.05), 0 0 0 2rem rgba(0,0,0,.05);
      flex-shrink: 0;
}

.relation_header__data{
      height: 168px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-end;
}

.relation_header__data h2{
      text-transform: uppercase;
      font-size: 5rem;
      line-height: 4rem;
}

.relation_header__data p{
      display: flex;
      align-items: center;
}

.relation_header__data p strong{
      color: var(--theme);
      font-size: 3rem;
      margin-right: 1rem;
}

.relation_header__data p a{
      background: var(--gradient-90);
      padding: .5rem 2rem;
      color: var(--light);
      text-transform: uppercase;
      font-weight: 500;
      border-radius: 999px;
}

.relation_content{
      width: 100%;
      height: 140px;
      padding-left: 84px;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha), var(--theme);
      margin-top: 1.5rem;
}

.relation_content p{
      color: var(--grys-300);
      font-size: 1.3rem;
}

.relation_footer{
      width: 100%;
      height: 166px;
      margin-top: 2rem;
      color: var(--theme);
      display: flex;
      align-items: center;
      gap: 2rem;
      padding-left: 3rem;
      font-family: var(--ft-3);
}

.relation_footer p{
      flex-grow: 1;
      padding: 0 1rem;
      font-size: 3rem;
      text-align: center;
}

.relation_time{
      width: 170px;
      height: 166px;
      position: relative;
}

.relation_time__box{
      width: 170px;
      height: 166px;
      background: var(--theme);
      color: var(--light);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
      text-transform: uppercase;
      flex-shrink: 0;
}

.relation_time__box strong{
      font-size: 4.5rem;
      font-weight: 700;
      line-height: 5rem;
}

.relation_time__box span{
      font-size: 3.5rem;
      font-weight: 200;
}

.relation_icon{
      width: 54px;
      height: 54px;
      border-radius: 50%;
      background: var(--light);
      position: absolute;
      top: 0;
      right: 0;
      display: flex;
      align-items: center;
      justify-content: center;
}

.relation_icon *{
      font-size: 2.5rem;
      color: var(--second);
}