.load_content{
      position: fixed;
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background: var(--grad-bg);
      z-index: 999;
}

.load_circle{
      width: 80px;
      height: 80px;
      position: relative;
}

.load_circle:before,
.load_circle:after{
      top: 50%;
      left: 50%;
      border-radius: 50%;
      transform: translate(-50%, -50%) rotate(0);
}

.load_circle:before{
      width: 76px;
      height: 76px;
      box-shadow: 0 0 0 .3rem var(--light);
}

.load_circle:after{
      width: 72px;
      height: 72px;
      border: solid .8rem var(--theme);
      border-left: solid .8rem transparent;
      border-right: solid .8rem transparent;
      border-bottom: solid .8rem transparent;
      animation: spinner 1s linear infinite;
}

@keyframes spinner{
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

.load_text{
      margin-top: 2rem;
}

.load_text p{
      text-transform: uppercase;
      font-size: 1.2rem;
      font-weight: 500;
      color: var(--light);
}