.banner_content{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      background: var(--gradient-135);
}

.banner_content img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      filter: grayscale(1);
      mix-blend-mode: overlay;
      opacity: .8;
}