.page_aside{
      position: absolute;
      top: 0;
      right: 0;
      width: 80px;
      height: 660px;
      background: var(--gradient);
      border-radius: 200px 0 0 200px;
      display: flex;
      flex-direction: column;
      align-items: center;
      z-index: 51;
}

.page_aside__button{
      width: 38px;
      margin-top: 5rem;
}

.page_aside__button button{
      width: 38px;
      height: 38px;
      border-radius: 50%;
      background: var(--alpha);
      display: flex;
      align-items: center;
      justify-content: center;
}

.page_aside__button button *{
      color: var(--light);
      font-size: 2rem;
}

.page_aside__button span{
      display: block;
      text-align: center;
      text-transform: uppercase;
      font-weight: 500;
      margin-top: .5rem;
}

.page_aside__title{
      width: 100%;
      display: flex;
      justify-content: center;
      margin-top: 2rem;
}

.page_aside__title h1{
      text-transform: uppercase;
      font-size: 5.5rem;
      font-family: var(--ft-2);
      font-weight: 900;
      letter-spacing: -1.5rem;
      text-align: center;
      color: var(--alpha);
      writing-mode: vertical-rl;
      text-orientation: upright;
}