.page_wrapper{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: rgba(0,0,0,.5);
      z-index: 50;
      opacity: 0;
      visibility: hidden;
}

.page_wrapper.show{
      animation: show_page .3s ease-in-out forwards;
}

@keyframes show_page{
      0%{
            opacity: 0;
            visibility: hidden;
      }
      100%{
            opacity: 1;
            visibility: visible;
      }
}

.page_content{
      position: absolute;
      width: 630px;
      height: 660px;
      background: var(--light);
      top: 70%;
      left: 130px;
      transform: translateY(-50%);
      opacity: 0;
      visibility: hidden;
      padding: 4rem;
}

.page_content.show{
      animation: show_page__inner .5s .3s ease-out forwards;
}

@keyframes show_page__inner{
      0%{
            top: 70%;
            opacity: 0;
            visibility: hidden;
      }
      60%{
            top: 40%;
            opacity: 1;
            visibility: visible;
      }
      100%{
            top: 50%;
            opacity: 1;
            visibility: visible;
      }
}