.empty_content{
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 2rem;
      padding: 4rem;
}

.empty_icon *{
      color: var(--second);
      font-size: 8rem;
}

.empty_content h2{
      text-align: center;
      font-size: 2rem;
      color: var(--alpha);
}