@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@200;300;400;700;900&family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap');

:root{
  /** fonts */
  --ft-1: 'Poppins', sans-serif;
  --ft-2: 'Inter', sans-serif;
  --ft-3: 'Raleway', sans-serif;

  /** colors */
  --green: #45d985;
  --light: #fff;
  --red-200: #d11e22;
  --red-100: #e90d0f;
  --alpha: rgba(0,0,0,.2);
  --grys-300: #9e9e9e;
  --grys-200: #e9e9e9;
  --grys-100: #f5f5f5;

  /** default theme */
  --theme: #b65bcb;
  --second: #1cd0e9;
  --dark-200: #0a2427;
  --dark-100: #321c37;
  --gradient-90: linear-gradient(90deg, var(--theme), var(--second));
  --gradient-45: linear-gradient(45deg, var(--theme), var(--second));
  --gradient-135: linear-gradient(135deg, var(--theme), var(--second));
  --gradient: linear-gradient(var(--theme), var(--second));
  --grad-bg: linear-gradient(135deg, var(--dark-100), var(--dark-200));

  /** theme2 */
  /*--theme: #ff4ba7;
  --seconde: #ffda64;

  /** theme 3*/
  /*--theme: #2b7bfa;
  --second: #a6ff52;

  /** theme 4 */
  /*--theme: #5cb270;
  --second: #f4f269;

  /** theme 5 */
  /*--theme: #2d00f7;
  --second: #f20089;

  /** theme 6 */
  /*--theme: #ff034c;
  --second: #ffca7a;

  /** theme 7 */
  /*--theme: #ff7900;
  --second: #9d4edd;

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;
  
  /** globals */
  font-family: var(--ft-1);
  font-size: 62.5%;
  color: var(--light);

}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: auto;
  min-height: 100vh;
  overflow-x: hidden;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--grad-bg);
}

body *{
  font-size: 1rem;
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
  font-family: var(--ft-2);
}

.bg_grad{
  display: inline-block;
  color: transparent;
  background: var(--gradient-45);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
}

::-webkit-scrollbar{
  width: .8rem;
  background: var(--alpha);
}

::-webkit-scrollbar-thumb{
  background: var(--theme);
}