.btn_social{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      background: var(--alpha);
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .2s;
}

.btn_social *{
      color: var(--second);
      font-size: 1.6rem;
}

.btn_social:hover *{
      color: var(--light);
}