.navigator{
      position: absolute;
      top: 0;
      left: 0;
      width: 120px;
      height: 100%;
      background: var(--gradient);
      border-top-right-radius: 200px;
      border-bottom-right-radius: 200px;
      z-index: 100;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.nav_avatar,
.nav_avatar img{
      width: 70px;
      height: 70px;
      border-radius: 50%;
      overflow: hidden;
      display: flex;
}

.nav_avatar{
      margin-top: 9rem;
      background: var(--alpha);
      box-shadow: 0 0 0 .5rem var(--alpha);
      flex-shrink: 0;
}

.nav_menu{
      width: 100%;
      height: auto;
      margin-top: 10rem;
}

.nav_menu ul{
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: .3rem;
}

.nav_menu ul li,
.nav_menu ul li a{
      width: 100%;
      height: 38px;
      display: flex;
      align-items: center;
      gap: .5rem;
      padding-left: 2rem;
      border-radius: 0 3.8rem 3.8rem 0;
      background: transparent;
      transition: .2s;
      cursor: pointer;
}

.nav_menu ul li:hover,
.nav_menu ul li.active{
      background: var(--light);
}

.nav_menu ul li:last-child{
      padding: 0;
}

.nav_menu ul li *,
.nav_menu ul li a *{
      font-size: 2rem;
      color: var(--light);
}

.nav_menu ul li span{
      text-transform: uppercase;
      width: fit-content;
      color: var(--light);
      font-size: 1.2rem;
      font-weight: 500;
}

.nav_menu ul li:hover *,
.nav_menu ul li.active *{
      color: var(--second);
}

.nav_menu ul li:hover span,
.nav_menu ul li.active span{
      color: transparent;
      background: var(--gradient-90);
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
}