.aside_content{
      width: 370px;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      z-index: 10;
      background-image: url(../../assets/bg.svg) , var(--gradient-90);
      background-size: cover;
      background-repeat: no-repeat;
}

.aside_content:before{
      width: 100%;
      height: 390px;
      background: var(--light);
      top: calc(50% - 195px);
      left: 0;
}

.aside_top{
      position: relative;
      margin-top: 5.5rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 2rem;
}

.aside_top p{
      font-size: 1.4rem;
}

.aside_top__title{
      width: fit-content;
      max-width: 100%;
      display: flex;
      flex-direction: column;
      text-transform: uppercase;
      margin-top: 1rem;
      align-items: flex-end;
}

.aside_top__title h1{
      font-size: 4.5rem;
      line-height: 4rem;
}

.aside_top__title h2{
      color: var(--alpha);
      font-size: 4rem;
      line-height: 3.5rem;
}

.aside_user{
      width: 350px;
      height: 410px;
      position: absolute;
      top: calc(50% - 205px);
      left: 10px;
      background: var(--light);
      border-radius: 1rem;
      box-shadow: 0 0 20px rgba(0,0,0,.2);
      padding: 3rem;
}

.aside_user__header{
      height: 70px;
      display: flex;
      align-items: center;
      gap: .5rem;
}

.aside_user__header_avatar{
      width: 70px;
      height: 70px;
      border-radius: 50%;
      background: var(--gradient-135);
      position: relative;
      flex-shrink: 0;
}

.aside_user__header_avatar:before{
      width: 64px;
      height: 64px;
      background: var(--light);
      border-radius: 50%;
      top: 3px;
      left: 3px;
}

.aside_user__header_avatar img{
      width: 60px;
      height: 60px;
      border-radius: 50%;
      position: relative;
      margin: 5px;
}

.user_online{
      width: 14px;
      height: 14px;
      background: var(--grys-100);
      border-radius: 50%;
      position: absolute;
      right: .4rem;
      bottom: .4rem;
}

.user_online.green{
      background: var(--green);
}

.user_online.red{
      background: var(--red-100);
}

.aside_user__data strong{
      font-size: 1.4rem;
}

.aside_user__data span{
      color: var(--theme);
      display: block;
      padding: .4rem 1.5rem;
      border-radius: 999px;
      background: var(--grys-100);
      width: fit-content;
      text-transform: uppercase;
}

.aside_user_content{
      width: 100%;
      height: 130px;
      margin-top: 2rem;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha) var(--theme);
}

.aside_user_content p{
      color: var(--grys-300);
      font-size: 1.2rem;
}

.aside_user__footer{
      margin-top: 2rem;
}

.user_location{
      display: flex;
      align-items: flex-end;
      gap: .5rem;
}

.user_location__icon *{
      font-size: 2.4rem;
      stroke: var(--theme);
}

.user_location__data{
      display: flex;
      flex-direction: column;
}

.user_location__data span{
      text-transform: uppercase;
      color: var(--second);
}

.user_location__data strong{
      font-size: 1.2rem;
}

.user_menu{
      margin-top: 2.5rem;
      width: 100%;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
}

.user_menu a{
      width: 40px;
      height: 40px;
      background: var(--grys-100);
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
}

.user_menu a *{
      font-size: 2rem;
      color: var(--second);
      stroke: var(--second);
}

.user_menu a:before{
      content: attr(data-tooltip);
      background: linear-gradient(18deg, var(--second) 40%, var(--theme));
      padding: .5rem 1.5rem;
      border-radius: .3rem;
      color: var(--light);
      text-transform: uppercase;
      top: -4rem;
      z-index: 2;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      visibility: hidden;
      transition: .2s;
}

.user_menu a:after{
      width: 0;
      height: 0;
      border-top: solid 10px var(--second);
      border-left: solid 10px transparent;
      border-right: solid 10px transparent;
      top: -1.5rem;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      visibility: hidden;
      transition: .2s;
}

.user_menu a:hover:before,
.user_menu a:hover:after{
      opacity: 1;
      visibility: visible;
}

.aside_footer{
      width: 100%;
      height: auto;
      padding: 3rem 2rem;
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
}

.aside_footer__id{
      width: fit-content;
      text-transform: uppercase;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
}

.aside_footer__id h2{
      font-size: 6rem;
      line-height: 5rem;
}

.aside_footer__social{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 1rem;
}

.aside_footer__social span{
      text-transform: uppercase;
      font-size: 1.2rem;
      font-weight: 500;
}

.social_menu{
      margin-top: 1rem;
      width: 100%;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: .8rem;
}