.friend_content{
      width: 460px;
      height: 580px;
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
      overflow-x: hidden;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--alpha), var(--theme);
}

.friend_row{
      width: 320px;
      height: 90px;
      background: var(--grys-100);
      border-radius: .6rem;
      box-shadow: 2rem 2rem 20px rgba(0,0,0,.2);
      overflow: hidden;
      display: flex;
      position: relative;
}

.friend_row__avatar{
      width: 90px;
      height: 90px;
      background: var(--grys-100);
      position: relative;
      padding: .5rem;
      flex-shrink: 0;
      z-index: 2;
}

.friend_row__avatar img{
      width: 60px;
      height: 60px;
      border-radius: .4rem;
      box-shadow: 1.5rem 1.5rem 15px rgba(0,0,0,.2);
}

.friend_row__data{
      flex-grow: 1;
      height: 90px;
      padding: 1rem;
      position: relative;
}

.friend_row__data strong{
      font-size: 1.6rem;
      font-weight: 700;
}

.friend_row__data p{
      font-size: 1.2rem;
      color: var(--grys-300);
}

.friend_row__data a{
      position: absolute;
      bottom: 1rem;
      right: 1rem;
      padding: .4rem 1.5rem;
      background: var(--gradient-90);
      color: var(--light);
      text-transform: uppercase;
      border-radius: 999px;
      font-weight: 500;
}