.video_content{
      width: 470px;
      height: 580px;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      justify-content: center;
      z-index: 10;
}

.video_content h3{
      color: var(--second);
      text-transform: uppercase;
      font-size: 1.4rem;
      font-weight: 400; 
      text-align: center;
}

.video_box{
      width: 400px;
      height: 280px;
      position: relative;
      margin-top: 2rem;
}

.video_box:before{
      width: 140px;
      height: 140px;
      border-radius: 50%;
      background: var(--gradient-45);
      bottom: -30px;
      left: -15%;
      z-index: -2;
}

.video_box__iframe{
      width: 400px;
      height: 226px;
      background: #121212;
      box-shadow: 4rem 4rem 25px rgba(0,0,0,.4);
      position: relative;
}

.video_box__iframe:before{
      width: 100%;
      height: .5rem;
      background: var(--gradient-90);
      bottom: -.5rem;
      left: 0;
}

.video_box__iframe iframe{
      border: none;
}