.background_content{
      width: 100%;
      height: 100%;
      display: grid;
      place-content: center;
      overflow: hidden;
      position: fixed;
      top: 0;
      left: 0;
      background: linear-gradient(135deg, var(--light) 20%, transparent 20.1%, transparent 80%, var(--light) 80.1%)
}

.background_inner{
      width: 800px;
      height: 820px;
      positioN: relative;
}

.circle_bg{
      width: 50rem;
      height: 50rem;
      border-radius: 50%;
      position: absolute;
      z-index: -1;
}

.circle_bg:nth-child(1){
      background: var(--gradient-45);
      left: 0;
      bottom: 0;
      transform: translate(-100%, 70%);
}

.circle_bg:nth-child(2){
      background: var(--gradient-135);
      right: 0;
      top: 0;
      transform: translate(100%, -100%);
}

.square_bg{
      width: 250px;
      height: 250px;
      position: absolute;
      z-index: -1;
}

.square_bg:nth-child(3){
      top: 0;
      left: 0;
      border: solid 7rem var(--theme);
      transform: translate(-40%, 40%) rotate(45deg);
}

.square_bg:nth-child(4){
      right: 0;
      bottom: 0;
      border: solid 7rem var(--second);
      transform: translate(60%, -40%) rotate(45deg);
}